import type {FC, DetailedHTMLProps, HTMLAttributes, PropsWithChildren} from 'react'
import type {WidthHeight} from './WidthHeight'
import type {LeftRightTopBottom} from './LeftRightTopBottom'
import type {MinMaxWidthHeight} from './MinMaxWidthHeight'

export type ReactDivProps = DetailedHTMLProps<
  HTMLAttributes<HTMLDivElement>,
  HTMLDivElement
>
export type DivProps = ReactDivProps &
  PropsWithChildren<WidthHeight> &
  LeftRightTopBottom &
  MinMaxWidthHeight & {
    src?: string
  }

/* 
  CSS backgroundImage: url(src) src     .     :
  URL:         .  , backgroundImage: url('https://example.com/image.png')   1.
   :          .  , backgroundImage: url('../images/background.png')    2.
   :         .  , backgroundImage: url('/images/background.png')    2.
   URI:   Base64      .  , backgroundImage: url('data:image/png;base64,iVBORw0KGgo...')    .
  : CSS       .  , backgroundImage: linear-gradient(to right, red, orange)    1.
           .  , backgroundImage: url('image1.png'), url('image2.png')       1.
          ,      background-color     1.
*/
// prettier-ignore
export const Div: FC<DivProps> = ({
  width, height, 
  style: _style, 
  src, 
  className: _className, 
  left, right, top, bottom,
  minWidth, maxWidth, minHeight, maxHeight,
  ...props
}) => {
  const style = {
    ..._style, 
    width, height, 
    backgroundImage: src && `url(${src})`,
    left, right, top, bottom,
    minWidth, maxWidth, minHeight, maxHeight
  }
  const className = ['box-border', src && 'bg-gray-300', _className].join(' ')
  return <div {...props} className={className} style={style} />
}
